.iron-image__container {

  width: 320px;
  height: 180px;


  background-size: 100% 100%;
}

.iron-image {
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  height: 180px;
  width: 320px;

  opacity: 0;

  -webkit-transition: opacity 1s ease;

  -o-transition: opacity 1s ease;

  transition: opacity 1s ease;
}

.iron-image--fade-in {
  opacity: 1;
}



